/* eslint-disable no-undef */
const riskyPatterns = new Set(["urgent", "password", "verify", "security alert", "suspicious", "phishing"]);

// Retrieves only the original email body (excluding the new reply text)
async function getOriginalEmailBody(): Promise<string> {
  return new Promise((resolve, reject) => {
    Office.context.mailbox.item.body.getAsync(Office.CoercionType.Text, (asyncResult) => {
      if (asyncResult.status === Office.AsyncResultStatus.Succeeded) {
        let fullBody = asyncResult.value;

        // Split at common reply separators (Outlook, Gmail, etc.)
        const replySeparatorPatterns = [
          /On .* wrote:/gi,
          /From: .*[\r\n]/gi,
          /Sent: .*[\r\n]/gi,
          /Subject: .*[\r\n]/gi,
        ];

        for (const pattern of replySeparatorPatterns) {
          const match = fullBody.match(pattern);
          if (match) {
            fullBody = fullBody.split(match[0])[1] || "";
            break;
          }
        }

        resolve(fullBody.trim());
      } else {
        reject(asyncResult.error);
      }
    });
  });
}

// Validates the email before sending.
// Validates the email before sending.
async function validateBeforeSend(event: { completed: (result?: { allowEvent: boolean }) => void }) {
  console.log("Validating original email content before send...");

  try {
    // Get original email body (excluding new reply text)
    const originalEmailBody = await getOriginalEmailBody();

    // Check for risky words
    const foundPatterns = [...riskyPatterns].filter((pattern) => originalEmailBody.toLowerCase().includes(pattern));

    if (foundPatterns.length > 0) {
      console.log("Risky Patterns Found in Original Email:", foundPatterns);

      // First, remove any existing notifications
      Office.context.mailbox.item.notificationMessages.removeAsync("blockWarning", (removeResult) => {
        if (removeResult.status === Office.AsyncResultStatus.Succeeded) {
          // Now add the new notification message
          Office.context.mailbox.item.notificationMessages.addAsync(
            "blockWarning",
            {
              type: "errorMessage",
              message: "The add-in FinShield is preventing this message from being sent.",
            },
            (addResult) => {
              if (addResult.status !== Office.AsyncResultStatus.Succeeded) {
                console.error("Failed to display warning:", addResult.error);
              }
              // Block sending after notification
              event.completed({ allowEvent: false });
            }
          );
        } else {
          console.error("Failed to remove existing notification:", removeResult.error);
          event.completed({ allowEvent: false });
        }
      });

      return;
    }

    // If no risky words, allow sending
    console.log("No risky patterns detected in the original email. Allowing send.");
    event.completed({ allowEvent: true });
  } catch (error) {
    console.error("Error during validation:", error);

    // Allow sending in case of error
    event.completed({ allowEvent: true });
  }
}


export { validateBeforeSend };
