/* eslint-disable no-undef */
/* eslint-disable prettier/prettier */
export const LoginOrGetValidToken = async () => {
    const token = await getValidAccessToken();
    if (token) {
        console.log("Valid token found, checking email safety...", token);
        return token;
    } else {
        console.log("No valid token found, initiating login...");
        return await initiateLogin(); // Wait for login process to complete
    }
};

const initiateLogin = async (): Promise<string | null> => {
    return new Promise((resolve) => {
        const authUrl =
            "https://login.microsoftonline.com/common/oauth2/v2.0/authorize?client_id=53ad7381-eedb-4d82-982b-a45b7b29df11&response_type=code&redirect_uri=https://localhost:3000/assets/redirect.html&scope=user.read+mail.send+Mail.ReadBasic+Mail.Read+Mail.ReadWrite+offline_access&response_mode=fragment&state=12345&nonce=678910";

        Office.context.ui.displayDialogAsync(
            authUrl,
            { height: 70, width: 40 },
            (asyncResult: Office.AsyncResult<Office.Dialog>) => {
                if (asyncResult.status === Office.AsyncResultStatus.Failed) {
                    console.error("Failed to open login dialog:", asyncResult.error?.message);
                    resolve(null);
                    return;
                }

                const loginDialog = asyncResult.value;

                loginDialog.addEventHandler(
                    Office.EventType.DialogMessageReceived,
                    (arg: any) => {
                        const authCode: string = arg.message;
                        console.log("Received auth code:", authCode);

                        if (authCode) {
                            getTokens(authCode).then(resolve); // Resolve with token once obtained
                        } else {
                            resolve(null);
                        }

                        loginDialog.close();
                    }
                );
            }
        );
    });
};

const getTokens = (authCode: string): Promise<string | null> => {
    return new Promise((resolve) => {
        const myHeaders = new Headers();
        myHeaders.append("Content-Type", "application/json");

        const raw = JSON.stringify({ auth_code: authCode });

        const requestOptions: RequestInit = {
            method: "POST",
            headers: myHeaders,
            body: raw,
            redirect: "follow",
        };

        fetch("http://127.0.0.1:4000/api/login/auth/microsoft", requestOptions)
            .then((response) => response.json())
            .then((result) => {
                if (result.access_token) {
                    storeTokens(result);
                    console.log("Tokens stored", result);
                    resolve(result.access_token);
                } else {
                    console.error("Failed to retrieve tokens.");
                    resolve(null);
                }
            })
            .catch((error) => {
                console.error("Error fetching tokens:", error);
                resolve(null);
            });
    });
};

const storeTokens = (responseData: { access_token: string; refresh_token: string; access_token_expiration: string }) => {
    localStorage.setItem("access_token", responseData.access_token);
    localStorage.setItem("refresh_token", responseData.refresh_token);
    localStorage.setItem("access_token_expiration", responseData.access_token_expiration);
};

const isAccessTokenExpired = (): boolean => {
    const expiration = localStorage.getItem("access_token_expiration");
    if (!expiration) return true;

    const expirationDate = new Date(expiration);
    return new Date() > expirationDate;
};

const getValidAccessToken = async (): Promise<string | null> => {
    if (isAccessTokenExpired()) {
        const refresh_token = localStorage.getItem("refresh_token");

        if (!refresh_token) {
            console.error("No refresh token available, login required");
            return null;
        }

        const requestOptions: RequestInit = {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({ refresh_token }),
        };

        return fetch("http://127.0.0.1:4000/api/login/auth/refresh", requestOptions)
            .then((response) => response.json())
            .then((result) => {
                if (result.access_token) {
                    storeTokens(result);
                    console.log("Token refreshed:", result);
                    return result.access_token;
                } else {
                    console.error("Failed to refresh token, login required");
                    return null;
                }
            })
            .catch((error) => {
                console.error("Error refreshing token:", error);
                return null;
            });
    } else {
        const token = localStorage.getItem("access_token");
        return token || null;
    }
};