/* eslint-disable no-undef */
import { Email, EmailCategory } from "./types";

type ProgressCallback = (progress: number, totalProcessed: number) => void;

const MAX_RETRIES = 3;
const RETRY_DELAY = 1000;

const updateCategoryWithRetry = async (
  email: Email,
  accessToken: string,
  desiredCategory: string
): Promise<boolean> => {
  for (let attempt = 1; attempt <= MAX_RETRIES; attempt++) {
    try {
      await fetch(`https://graph.microsoft.com/v1.0/me/messages/${email.id}`, {
        method: "PATCH",
        headers: {
          Authorization: `Bearer ${accessToken}`,
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ categories: [desiredCategory] }),
      });
      return true;
    } catch (error) {
      if (attempt === MAX_RETRIES) {
        console.error(`Failed to update email ${email.id} after ${MAX_RETRIES} attempts`);
        return false;
      }
      await new Promise((resolve) => setTimeout(resolve, RETRY_DELAY * attempt));
    }
  }
  return false;
};

export const updateCategories = async (
  emails: Email[],
  accessToken: string,
  progressCallback?: ProgressCallback
): Promise<{ succeeded: Email[]; failed: Email[] }> => {
  const results = { succeeded: [], failed: [] };
  const total = emails.length;

  for (let i = 0; i < total; i++) {
    const email = emails[i];
    const desiredCategory = EmailCategory[email.detectedCategory];
    const needsUpdate = !email.currentCategory.includes(desiredCategory);

    if (!needsUpdate) {
      progressCallback?.(((i + 1) / total) * 100, i + 1);
      results.succeeded.push(email);
      continue;
    }

    const success = await updateCategoryWithRetry(email, accessToken, desiredCategory);

    if (success) {
      results.succeeded.push(email);
    } else {
      results.failed.push(email);
    }

    progressCallback?.(((i + 1) / total) * 100, i + 1);
  }

  return results;
};
