import { LoginOrGetValidToken } from "../taskpane/auth/Auth";
import { fetchEmails } from "../taskpane/utilities/categorizeEmails/fetchEmails";
import { updateCategories } from "../taskpane/utilities/categorizeEmails/updateCategories";
import { validateBeforeSend } from "../taskpane/utilities/validateBeforeSend";

/* global Office */

Office.onReady(() => {
  console.log("Office.js is ready.");

  // Register functions with Office actions
  Office.actions.associate("CategorieEmails", CategorieEmails);
  Office.actions.associate("validateBeforeSend", validateBeforeSend);
  Office.actions.associate("onNewMessageComposeHandler", validateBeforeSend);
});

const CategorieEmails = async (event: { completed: (arg0: { allowEvent: boolean }) => void }) => {
  let isSuccess = false;

  try {
    const accessToken = await LoginOrGetValidToken();
    const emails = await fetchEmails(accessToken);

    await updateCategories(emails, accessToken);

    isSuccess = true;

    Office.context.mailbox.item.notificationMessages.addAsync("successMessage", {
      type: Office.MailboxEnums.ItemNotificationMessageType.InformationalMessage,
      message: "FinShield has successfully categorized your emails.",
      icon: "icon16", // Replace with your add-in icon ID
      persistent: false,
    });
  } catch (error) {
    console.error("Error categorizing emails:", error);

    Office.context.mailbox.item.notificationMessages.addAsync("errorMessage", {
      type: Office.MailboxEnums.ItemNotificationMessageType.ErrorMessage,
      message: "FinShield couldn't categorize your emails. Please try again.",
    });
  } finally {
    event.completed({ allowEvent: isSuccess });
  }
};
