import { EmailCategory } from "./types";

const riskyPatterns = new Set(["urgent", "password", "verify", "security alert", "suspicious", "phishing"]);
const neutralPatterns = new Set(["invoice", "payment", "statement", "receipt"]);

export const analyzeEmail = (subject?: string, body?: string): keyof typeof EmailCategory => {
  if (!subject && !body) return "Safe"; // Early return for empty input

  const content = `${subject || ""} ${body || ""}`.toLowerCase().trim();

  for (const pattern of Array.from(riskyPatterns)) {
    if (content.includes(pattern)) return "Risky";
  }

  for (const pattern of Array.from(neutralPatterns)) {
    if (content.includes(pattern)) return "Neutral";
  }

  return "Safe";
};
