/* eslint-disable prettier/prettier */
import { analyzeEmail } from "./analyzeEmail";
import { Email, EmailCategory } from "./types";

export const fetchEmails = async (accessToken: string): Promise<Email[]> => {
  try {
    const response = await fetch(
      "https://graph.microsoft.com/v1.0/me/mailfolders/inbox/messages?$select=id,subject,from,bodyPreview,categories,toRecipients&$top=500",
      { headers: { Authorization: `Bearer ${accessToken}` } }
    );

    const data = await response.json();
    if (!data.value) return [];
    console.log(data.value);
    const emails: Email[] = await Promise.all(
      data.value.map(async (email: any) => ({
        id: email.id,
        to: email.toRecipients?.[0]?.emailAddress?.address || "Unknown",
        subject: email.subject || "No Subject",
        sender: email.from?.emailAddress?.address || "Unknown",
        body: email.bodyPreview || "",
        currentCategory: email.categories?.[0] || EmailCategory.Uncategorized,
        detectedCategory: await analyzeEmail(email.subject, email.bodyPreview),
      }))
    );
    return emails;
  } catch (error) {
    console.error("Error fetching emails:", error);
    return [];
  }
};
