// types.ts - No changes needed
export enum EmailCategory {
  Safe = "Green Category",
  Neutral = "Yellow Category",
  Risky = "Red Category",
  Uncategorized = "Uncategorized",
}

export interface Email {
  id: string;
  to: string;
  subject: string;
  sender: string;
  body: string;
  currentCategory: string;
  detectedCategory: keyof typeof EmailCategory;
}